import UsersModel from '../models/user';

export default async function getUserDeviceTokens(ids) {
  const Users = UsersModel();
  const usersInDB = await Users.find({ _id: { $in: ids } }).select(
    'deviceToken'
  );
  return usersInDB
    .filter((user) => user.deviceToken)
    .map((user) => user.deviceToken);
}
